.floor_center {
    margin-top: 120px;
    position: relative;

    &--item_wrap {
        // display: none;
    }
    // display: none;
    @media all and (max-width: 1023px) {
        display: none;
    }
    &.active {
        display: block;
    }
    .act_vis, .act_vis1, .act_vis2, .act_vis3 {
        &.active {.value {
            color: #957B61;
        }
            
    }
    }
    .apart_popup {
        position: absolute;
        z-index: 3;
        pointer-events: none;
    }
    &__svg {
        max-width: 1460px;
        width: 100%;    
        margin-left: auto;
        margin-right: auto;
        position: relative;
        margin-top: 18em;
        margin-bottom: 18em;
        svg {
            *.active {
                transition: all .3s ease-out;
                cursor: pointer;
                &:hover {
                    fill: #fff !important;
                    opacity: .5 !important;
                }
            }
            *:not(.active):not(.image) {
                fill: none;
            }
        }
    }
    &.around1 {
        .floor_center__svg:before {
            pointer-events: none;
            content: '';
            position: absolute;
            top: -18em;
            right: 0;
            bottom: -18em;
            left: 0;
            background: url(../img/img27.png) center center / contain no-repeat;
            z-index: 0;
        }
    }
    &.around2 {
        .floor_center__svg:before {
            pointer-events: none;
            content: '';
            position: absolute;
            top: -18em;
            right: 0;
            bottom: -18em;
            left: 0;
            background: url(../img/img26.png) center center / contain no-repeat;
            z-index: 0;
        }
    }
    &.around3 {
        .floor_center__svg:before {
            pointer-events: none;
            content: '';
            position: absolute;
            top: -18em;
            right: 0;
            bottom: -18em;
            left: 0;
            background: url(../img/img28.png) center center / contain no-repeat;
            z-index: 0;
        }
    }
    .apart_popup .value {
        height: 0;
        font-family: 'gmm', serif;
        width: 10em;
        text-align: center;
        top: 0;
        left: 0;
        line-height: 1em;
        z-index: 10;
        font-size: vw(18);
        margin: -0.5em -5em;
        font-weight: 800;
        -webkit-transition: color .2s ease;
        -moz-transition: color .2s ease;
        -ms-transition: color .2s ease;
        -o-transition: color .2s ease;
        transition: color .2s ease;
        color: #fff;
        sup {
            top: -8px;
            font-size: 11px;
        }
    }
    .main_floor {
        position: absolute;
        top: -2px;
        left: 2px;
        right: 0;
        bottom: 0;
    }
}
.hover_bl {
    background: #000000;
    max-width: 1460px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    opacity: 0.9;
    box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: space-between;
    padding: 40px 90px;    
    left: 0;
    right: 0;
    z-index: 10;
    bottom: -7em;
    position: absolute;
    &.hidden:not(.active) {
        display: none;
    }
    // opacity: 0;
    // visibility: hidden;
    pointer-events: none;
    transition: all .3s ease-out;
    sup {
        top: -8px;
        font-size: 11px;
    }
    @media all and (max-width: 1400px) {
        padding: 20px 40px;
    }
    &.active {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
    &__block {
        width: 33%;
        text-align: center;
        &:not(:last-child) {
            border-right: 1px solid #fff;
        }   
        &--title {
            font-family: 'gm', serif;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 26px;
            margin-bottom: 13px;
            color: #FFFFFF;
        }
        &--text {
            font-family: 'gm', serif;
            font-style: normal;
            font-weight: normal;
            font-size: 48px;
            text-transform: uppercase;
            color: #957B61;
            @media all and (max-width: 1400px) {
                font-size: 28px;
            }
        }
    }
}
.new_tabs {
    .floor_center__svg {
        margin-top: 0;
        margin-bottom: 0;
        svg {
            g {
                path {
                    pointer-events: none;
                    &.active {
                        fill: #fff !important;
                        opacity: .5 !important;
                    }
                }
            }
        }
        img {
            filter: contrast(0.5);
        }
        .main_floor {
            top: 0;
            left: 0;
        }
    }
    .floor_center {
        margin-top: 20px;
    }
}
